// export const FIXVERSION = 'fixVersion';
// export const BRUKERHISTORIE = 'brukerHistorie';
// export const TEST = 'test';
// export const FEIL = 'feil';
// export const COLLECTOR = 'collector';

export const PROSJEKT = 'PTINN';

const linkedIssueWrap = (oppgaver) => (oppgaver.map(oppgave => `linkedIssues(${oppgave})`));

/**
 * map[fane][rad]['element'] = {type = 'issue type', prev = 'reads issues from', next = 'provides issues to', format = 'formats linked issues', ?hasProject = 'adds PROJECT to url', ?hasVersion = 'adds fixversion to url'}
 * <- endre [fane] til ['fane'] ->
 */
/**
 * MAP: {
 *  Fane 1: {
 *    SP
 *    Epos
 *    Brukerhistorie
 *    Test
 *    Feil
 *  }
 *  Fane 2: {
 *    Collector
 *    Test
 *    Feil
 *    —————
 *    Epos
 *    Brukerhistorie
 *    Test
 *    Feil
 *  }
 * }
 */
export const segmentKart = [
  [
    {
      type: 'SP',
      start: true,
      formaterUrl()
        { return 'type = "SITS Bestilling"' }
    },
    {
      type: 'Epos',
      formaterUrl(oppgaver)
        { return `type = Epos AND project = ${PROSJEKT} AND issue in (${linkedIssueWrap(oppgaver)})` }
    },
    {
      type: 'Brukerhistorie',
      formaterUrl(oppgaver)
        { return `type = Brukerhistorie AND "Epic Link" in (${oppgaver})` }
    },
    {
      type: 'Test',
      formaterUrl(oppgaver)
        { return `type = Test AND issue in (${linkedIssueWrap(oppgaver)})` }
    },
    {
      type: 'Feil',
      slutt: true,
      formaterUrl(oppgaver)
        { return `type = Feil AND issue in (${linkedIssueWrap(oppgaver)})` }
    }  
  ],
  [
    {
      type: 'Collector Feil',
      start: true,
      trengerUtgivelse: true,
      formaterUrl()
        { return 'type = Feil AND labels = collector-a0f2b961' }
    },
    {
      type: 'Test',
      formaterUrl(oppgaver)
        { return `type = Test AND issue in (${linkedIssueWrap(oppgaver)})` }
    },
    {
      type: 'Feil',
      slutt: true,
      formaterUrl(oppgaver)
        { return `type = Feil AND issue in (${linkedIssueWrap(oppgaver)})` }
    },
    {
      type: 'Epos',
      start: true,
      trengerUtgivelse: true,
      formaterUrl()
        { return 'type = Epos' }
    },
    {
      type: 'Brukerhistorie',
      formaterUrl(oppgaver)
        { return `type = Brukerhistorie AND "Epic Link" in (${oppgaver})` }
    },
    {
      type: 'Test',
      formaterUrl(oppgaver)
        { return `type = Test AND issue in (${linkedIssueWrap(oppgaver)})` }
    },
    {
      type: 'Feil',
      slutt: true,
      formaterUrl(oppgaver)
        { return `type = Feil AND issue in (${linkedIssueWrap(oppgaver)})` }
    }
  ]
];

export default segmentKart;


