import React from 'react';

const Tips = () => (
  <div style={{textAlign: 'left', margin: '30px', width: '800px'}}>
    <h2>Før du bruker applikasjonen</h2>
    <br/>
    <h4>Cross-Origin Resource Sharing</h4>
    <p>JIRA åpner ikke for CORS på REST-kall ifra ukjente domener, som gjør at du må deaktivere sikkerhetsmekanismer i nettleseren din for å bruke applikasjonen (inntil videre). Gitt at du bruker Chome på et Linux-basert system:</p>
    <p>&nbsp;&nbsp;• Sørg for at du ikke har noen instanser av Chrome som kjører på maskinen.</p>
    <p>&nbsp;&nbsp;• Start chrome med flaggene <code>--disable-web-security</code> og <code>--user-data-dir</code> aktivert.</p>
    <p>e.g. <code>/opt/google/chrome/google-chrome --disable-web-security --user-data-dir</code></p>
    <p>Jeg ville forsåvidt ikke anbefalt å surfe for mye på internett i denne tilstanden.</p>
    <br/>
    <h4>Login</h4>
    <p>Login krever samme passord of brukernavn som JIRA brukeren din. Om du møter problemer med å logge inn kan det hende at CAPTCHA-systemet til JIRA har blitt aktivert, i hvilket tilfelle er du nødt til å besøke JIRA og (logge ut og) logge inn, da Atlassian ikke tilbyr noe bedre alternativ.</p>
    <br/>
    <h4>Feedback</h4>
    <p>Om du har noe på hjertet kan du kontakte meg via <i>Martin.Jonassen@skatteetaten.no</i></p>
  </div>
);

export default Tips;


