import React from 'react';
import { NavLink } from "react-router-dom";

export const NavBar = () => (
  <span style={{backgroundColor: '#222'}}>
      <NavLink to="/fane/1" style={{width: '33%', color: '#FFF', textDecoration: 'none', fontWeight: 'bold', display: 'inline-block'}}
               activeStyle={{backgroundColor: '#FFF', color: '#222'}}>
        Fane 1
      </NavLink>
      <NavLink to="/help" style={{width: '34%', color: '#FFF', textDecoration: 'none', fontWeight: 'bold', display: 'inline-block'}}
               activeStyle={{backgroundColor: '#FFF', color: '#222'}}>
        Tips
      </NavLink>
      <NavLink to="/fane/2" style={{width: '33%', color: '#FFF', textDecoration: 'none', fontWeight: 'bold', display: 'inline-block'}}
               activeStyle={{backgroundColor: '#FFF', color: '#222'}}>
        Fane 2
      </NavLink>
    </span>
);

export default NavBar;


