import React from 'react';
import PropTypes from 'prop-types';

import Oppgave from "../containere/Oppgave";

const OppgaveListe = ({kontekst, oppgaver}) => (
  <div className="ske-oppgaver">
    {Array.isArray(oppgaver) && oppgaver.map((oppgave) =>
      <Oppgave key={oppgave.id} kontekst={kontekst} oppgave={oppgave} />
    )}
  </div>
);

OppgaveListe.propTypes = {
  kontekst: PropTypes.shape({
    faneId: PropTypes.number,
    segmentId: PropTypes.number
  }).isRequired,
  oppgaver: PropTypes.array.isRequired
};

export default OppgaveListe;


