import React from 'react';
import { connect } from 'react-redux';

import { markerUtgivelseOgHentOppgaver } from '../handlinger/index';

const UtgivelseVelger = ({ kontekst, utgivelser, dispatch }) => (
  <select
    className="ske-utgivelse-filter"
    defaultValue="default"
    onChange={e => {
      e.preventDefault(); 
      dispatch(markerUtgivelseOgHentOppgaver(kontekst, e.target.value));
    }
  }>
    <option value="default" disabled hidden>Velg en utgivelse</option>
    {utgivelser.map(utgivelse => 
      <option key={utgivelse.id}>{utgivelse.name}</option>)
    }
  </select>
);

export default connect()(UtgivelseVelger);


