import React from 'react';

import SegmentContainer from '../containere/SegmentContainer';
import MAP from '../segmentKart';

const FANEID = 0; // todo: export map function to separate component

// Kontrollerer layout
const Panorama = () => (
  <section className="ske-panorama">
    {MAP[FANEID].map((segment, index) => 
      <SegmentContainer
        key={index}
        kontekst={{faneId: FANEID, segmentId: index}}
      />
    )}
  </section>
);

export default Panorama;


