import React from 'react';
import { connect } from 'react-redux';

import { autentiser } from '../handlinger';

const Login = ({ dispatch }) => {
let tekst, passord;

  return (
    <form onSubmit={e => {
      e.preventDefault();
      
      dispatch(autentiser(btoa(`${tekst.value}:${passord.value}`)));
    }}>
      
      <p>Logg inn før du kan bruke applikasjonen</p>
      <input type="text" placeholder="Brukernavn" ref={node => {tekst = node}}/>
      <input type="password" placeholder="Passord" ref={node => {passord = node}}/>
      <input type="submit" value="Login"/>
    </form>
)};

export default connect()(Login);


