import React from 'react';
import { Route } from 'react-router-dom';

import Login from '../containere/Login';

const LoginCheck = ({ erAutentisert, component: Component, ...rest }) => (
  <Route {...rest} render={props => 
    erAutentisert
    ? <Component {...rest} />
    : <Login/>
  }/>
);

export default LoginCheck;


