import React from 'react';
import PropTypes from 'prop-types';

import OppgaveListe from "./OppgaveListe";
import OppgaveJQLFilter from "../containere/OppgaveJQLFilter";

const Segment = ({kontekst, state}) => (
  <div className="ske-segment">
    <OppgaveJQLFilter kontekst={kontekst} />
    {(state.oppgaver && state.oppgaver.length)
      ? <OppgaveListe oppgaver={state.oppgaver} kontekst={kontekst} />
      : <div />
    }
  </div>
);

Segment.propTypes = {
  kontekst: PropTypes.shape({
    faneId: PropTypes.number,
    segmentId: PropTypes.number
  }).isRequired,
  state: PropTypes.object.isRequired
};

export default Segment;


