import React from 'react';
import { Route, Switch, Link, withRouter } from 'react-router-dom';
import { connect } from 'react-redux';

import '../App.css';
import Header from '../komponenter/Header';
import NavBar from '../komponenter/NavBar';
import Tips from '../komponenter/Tips';
import LoginRuter from '../komponenter/LoginCheck';
import Panorama from '../komponenter/Panorama';
import Cascade from '../komponenter/Cascade';

const App = ({erAutentisert, utgivelser}) => (
  <div className="App">
    <Header/>
    <NavBar/>
    <Switch>
      <Route path="/help" component={Tips}/>
      <LoginRuter erAutentisert={erAutentisert} path="/fane/1" component={Panorama}/>
      <LoginRuter erAutentisert={erAutentisert} path="/fane/2" component={Cascade} utgivelser={utgivelser}/>
      <Route component={render => <p>Velg en fane for å gjøre noe produktivt. Jeg anbefaler å starte med <Link to="/help">Tips</Link></p>}/>
    </Switch>
  </div>
);

const mapStateToProps = state => ({
  erAutentisert: state.legitimasjon.length > 0,
  utgivelser: state.utgivelser
})

export default withRouter(connect(mapStateToProps)(App));


