import MAP, { PROSJEKT } from '../segmentKart';
export const URL = 'https://aurora/jira';

export const MARKER_OPPGAVE = 'MARKER_OPPGAVE';
export const MOTTA_OPPGAVER = 'MOTTA_OPPGAVER';
export const MOTTA_UTGIVELSER = 'MOTTA_UTGIVELSER';
export const FILTRER_SOEK = 'FILTRER_SOEK';
export const FILTRER_UTGIVELSE = 'FILTRER_UTGIVELSE';
export const LOGIN = 'LOGIN';
export const ERROR = 'ERROR';

const mottaOppgaver = ({faneId, segmentId}, oppgaver) => ({
  type: MOTTA_OPPGAVER,
  faneId,
  segmentId,
  oppgaver: oppgaver.map((oppgave) => ({...oppgave, erMarkert: false}))
});

const mottaUtgivelser = utgivelser => ({
  type: MOTTA_UTGIVELSER,
  utgivelser
});

export const login = legitimasjon => ({
  type: LOGIN,
  legitimasjon
});

export const error = error => ({
  type: ERROR,
  error
});

const lagUrl = ({faneId, segmentId}, state) => {
  console.log({faneId, segmentId, state})
  let url = `${URL}/rest/api/2/search?jql=`;
  url += (MAP[faneId][segmentId].start)
    ? MAP[faneId][segmentId].formaterUrl()
    : MAP[faneId][segmentId].formaterUrl(
        state.faner[faneId][segmentId - 1].oppgaver
          .filter(oppgave => oppgave.erMarkert)
          .map(oppgave => oppgave.key)
      )
  if (state.faner[faneId][segmentId].soekefelt) url += ` AND ${state.faner[faneId][segmentId].soekefelt}`;
  if (MAP[faneId][segmentId].trengerUtgivelse) url += ` AND fixVersion = "${state.faner[faneId].utgivelse}"`

  return url;
}

const hentOppgaver = (kontekst, url, legitimasjon) => dispatch => {
  return fetch(url, {
    method: 'GET',
    headers: new Headers({
      'Content-Type': 'application/json',
      'Authorization': 'Basic ' + legitimasjon
    })
  }).then(respons => respons.json())
    .then(respons => !respons.errorMessages
      ? dispatch(mottaOppgaver(kontekst, respons.issues))
      : dispatch(mottaOppgaver(kontekst, [])))
    .catch(feilmelding => console.error('Fetch error:', feilmelding));
};

const hentVersjoner = legitimasjon => dispatch => {
  return fetch(URL + '/rest/api/2/project/' + PROSJEKT + '/version?orderBy=startDate', {
    method: 'GET',
    headers: new Headers({
      'Content-Type': 'application/json',
      'Authorization': 'Basic ' + legitimasjon
    })
  })
    // .then(respons => respons.json())
    // .then(respons => dispatch(mottaVersjoner(respons.values.filter(({ archived }) => !archived))))
    .catch(feilmelding => dispatch(error(feilmelding)));
}

export const soekOgHentOppgaver = 
  (kontekst, soekeVerdi) => 
    (dispatch, getState) => {
  const { faneId, segmentId } = kontekst;
  dispatch({
    type: FILTRER_SOEK,
    faneId,
    segmentId,
    soekeVerdi
  });
  // dispatch(wait)
  const url = lagUrl(kontekst, getState());
  return dispatch(hentOppgaver(kontekst, url, getState().legitimasjon));
}

export const markerOgHentOppgaver =
  (kontekst, oppgaveId) => 
    (dispatch, getState) => {
  const { faneId, segmentId } = kontekst;
  dispatch({
    type: MARKER_OPPGAVE,
    faneId,
    segmentId,
    oppgaveId
  });
  if (MAP[faneId][segmentId].slutt) return;
  const url = lagUrl({faneId, segmentId: segmentId + 1}, getState());
  return dispatch(hentOppgaver({faneId, segmentId: segmentId + 1}, url, getState().legitimasjon));
}

export const markerUtgivelseOgHentOppgaver = (kontekst, utgivelse) => (dispatch, getState) => {
  const { faneId } = kontekst;
  dispatch({
    type: FILTRER_UTGIVELSE,
    faneId,
    utgivelse
  });

  const state = getState();
  console.log("State:", state);

  MAP[faneId].forEach((segment, id) => {
    if (!segment.start) return;
    const url = lagUrl({faneId, segmentId: id}, state);
    dispatch(hentOppgaver({faneId, segmentId: id} ,url, state.legitimasjon));
  })
}

export const autentiser = legitimasjon => (dispatch, getState) => {
  dispatch(hentVersjoner(legitimasjon)).then(respons => respons.json())
    .then(respons => {
      if (!respons.errorMessages) {
        dispatch(mottaUtgivelser(respons.values.filter(({ archived }) => !archived)));
      dispatch(login(legitimasjon));
    } else {
      dispatch(error(respons.errorMessages));
    }
  }).catch(feilmelding => dispatch(error(feilmelding)));
  return Promise.resolve();
};


