import MAP from "../segmentKart";

const faner = (state = {
  0: {
    0: {},
    1: {},
    2: {},
    3: {},
    4: {}
  },
  1: {
    0: {},
    1: {},
    2: {},

    3: {},
    4: {},
    5: {},
    6: {}
  }
}, action) => {
  if (action.hasOwnProperty('faneId')) return ({
    ...state,
    [action.faneId]: segmenter(state[action.faneId], action)
  });
  return state;
};

const segmenter = (state, action) => {
  switch(action.type) {
    case 'MOTTA_OPPGAVER': 
      return oppdaterKommendeSegmenter(state, action);
    case 'FILTRER_UTGIVELSE':
      return {...state, utgivelse: action.utgivelse}
    default: // no default
  }

  if (action.hasOwnProperty('segmentId')) return ({
    ...state, 
    [action.segmentId]: segment(state[action.segmentId], action)
  })
  
  return state;
};

const segment = (state, action) => {
  switch (action.type) {
    case 'MARKER_OPPGAVE':
      return {...state,
        oppgaver: state.oppgaver.map(oppgave => (
          oppgave.id === action.oppgaveId
            ? {...oppgave, erMarkert: !oppgave.erMarkert}
            : oppgave
        ))
      }
    case 'FILTRER_SOEK':
      return {...state, soekefelt: action.soekeVerdi};
    default:
      return state;
  }
};

const oppdaterKommendeSegmenter = (segmenter, { faneId, segmentId, oppgaver = []}) => {
  if (MAP[faneId][segmentId].slutt || !segmenter[segmentId].oppgaver || segmenter[segmentId].oppgaver.length === 0) {
    return {
      ...segmenter, 
      [segmentId]: {...segmenter[segmentId], oppgaver}
    };
  }
  return {
    ...segmenter,
    ...oppdaterKommendeSegmenter(segmenter, {faneId, segmentId: segmentId + 1}),
    [segmentId]: {...segmenter[segmentId], oppgaver}
  };

}

export default faner;


