import React from 'react';

import SegmentContainer from '../containere/SegmentContainer';
import MAP from '../segmentKart';
import UtgivelseVelger from '../containere/UtgivelseVelger';

const FANEID = 1; // todo: export map function to separate compon

const Cascade = ({utgivelser}) => (
  <section className="ske-cascade">
    <UtgivelseVelger utgivelser={utgivelser} kontekst={{faneId: FANEID}}/>
    {MAP[FANEID].map((segment, index) => 
      <SegmentContainer
        key={index}
        kontekst={{faneId: FANEID, segmentId: index}}
      />
    )}
  </section>
);

export default Cascade;


