import React from 'react';
import PropTypes from 'prop-types';
import { connect } from 'react-redux';

import { URL, markerOgHentOppgaver } from "../handlinger/index";

// En enkel issue: ta utgangspunkt i JIRA sin utforming; ønskes status, assignee og fixed version(?); bryter-funksjonalitet for videre søk
let Oppgave = ({ kontekst, oppgave, dispatch }) => (
  <div className={`ske-oppgave ${oppgave.erMarkert === true && "ske-oppgave-markert"}`} onClick={() => dispatch(markerOgHentOppgaver(kontekst, oppgave.id))}>
    <div className="ske-oppgave-innhold">
      <div className="ske-oppgave-felter">
        <div className="ske-oppgave-type">
          <span>
            <img src={oppgave.fields.issuetype.iconUrl} alt="issue type" title={oppgave.fields.issuetype.name} />
          </span>
        </div>
        <div className="ske-oppgave-key">
          <a href={`${URL}/browse/${oppgave.key}`}>{oppgave.key}</a>
        </div>
        <div className="ske-oppgave-oppsummering">
          <span className="ske-oppgave-oppsumering-indre">{oppgave.fields.summary}</span>
        </div>
      </div>
    </div>
    <div className="ske-oppgave-avatar">
      { oppgave.fields.assignee === null || <img className="ske-oppgave-avatar-img" src={oppgave.fields.assignee.avatarUrls['48x48']} alt="brukeravatar"
                                                 title={oppgave.fields.assignee.displayName} /> }
    </div>
    <div className="ske-oppgave-flagg-prioritet">
      <span className="ske-oppgave-prioritet">
        { oppgave.fields.priority === null || <img src={oppgave.fields.priority.iconUrl} alt="prioritet" title={oppgave.fields.priority.name} /> }
      </span>
    </div>
    <div className="ske-oppgave-flagg-status">
      <span className="ske-oppgave-status">
        <img src={oppgave.fields.status.iconUrl} alt="status" title={oppgave.fields.status.name} />
      </span>
    </div>
  </div>
);

Oppgave.propTypes = {
  oppgave: PropTypes.object.isRequired,
  kontekst: PropTypes.shape({
    faneId: PropTypes.number,
    segmentId: PropTypes.number
  }).isRequired
};

Oppgave = connect()(Oppgave);

export default Oppgave;


