import React from 'react';
import PropTypes from 'prop-types';
import { connect } from 'react-redux';

import { soekOgHentOppgaver } from '../handlinger/index'
import MAP from '../segmentKart';

// Input bar for å filtrere Issues etter Jira Query Language: tar presets og freeform
let OppgaveJQLFilter = ({ kontekst, dispatch }) => {
  let input;

  return (
    <form className="ske-oppgave-filter" onSubmit={e => {
      e.preventDefault();

      dispatch(soekOgHentOppgaver(kontekst, input.value));
      input.placeholder = input.value;
      input.value = '';
    }}>
      <p style={{fontSize: 12}}>{MAP[kontekst.faneId][kontekst.segmentId].type}</p>
      <input
        className="ske-oppgave-jqlfilter"
        placeholder="JQL Query"
        ref={node => {
          input = node
        }}/>
    </form>
  )
};

OppgaveJQLFilter.propTypes = {
  kontekst: PropTypes.shape({
    faneId: PropTypes.number,
    segmentId: PropTypes.number
  }),
  dispatch: PropTypes.func.isRequired
}

OppgaveJQLFilter = connect()(OppgaveJQLFilter);

export default OppgaveJQLFilter;


